<template>
  <div @click="copy" class="cp">
    {{ text }}
    <span class="tooltiptext">{{ status }}</span>
  </div>
</template>

<script>
export default {
  name: "CopyPaste",
  props: {
    text: String,
  },
  data() {
    return {
      status: "Copy",
    };
  },
  methods: {
    copy() {
      const el = document.createElement("textarea");
      el.value = this.text;
      el.setAttribute("readonly", "");
      el.style.position = "absolute";
      el.style.left = "-9999px";
      document.body.appendChild(el);
      el.select();
      document.execCommand("copy");
      document.body.removeChild(el);
      this.status = "Copied!";
      setTimeout(() => {
        this.status = "Copy";
      }, 1500);
    },
  },
};
</script>

<style scoped>
.cp {
  cursor: pointer;
  background-color: #f6f8fa;
  padding: 10px;
  border-radius: 5px;
  font-family: SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  user-select: none;
  position: relative;
  display: inline-block;
}

.cp:hover {
  background-color: #cdf5d2;
}

.cp:active {
  background-color: #adccb1;
}

.cp:hover .tooltiptext {
  visibility: visible;
}

.tooltiptext {
  visibility: hidden;
  width: 120px;
  background-color: rgba(141, 224, 108, 0.733);
  color: #fff;
  text-align: center;
  border-radius: 6px;
  padding: 5px 0;

  position: absolute;
  z-index: 1;
  top: 105%;
  left: 50%;
  margin-left: -60px;
}
</style>
